    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes expand {
  from {
    max-height: 0px;
  }
  to {
    max-height: 1000px;
    opacity: 1;
  }
}

@keyframes wiggle {
  0%,
  100% {
    transform: rotate(-3deg);
  }
  50% {
    transform: rotate(3deg);
  }
}

@keyframes custom-ping {
  75%,
  100% {
    transform: scale(1.3);
    opacity: 0;
  }
}

.animate-spin {
  animation: spin 10s linear infinite;
}

.animate-bounce {
  animation: bounce 3s infinite;
}

.animate-wiggle {
  animation: wiggle 3s infinite;
}

.animate-grow {
  animation: grow 3s infinite;
}

.animate-grow-little {
  animation: grow-little 3s infinite;
}

.animate-expand {
  animation: expand 1s;
}

.animate-custom-ping {
  animation: custom-ping 2s infinite;
}

